import React from 'react';
import { Button, Paper } from '@material-ui/core';
import { AssignmentReturn } from '@material-ui/icons';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { FormProvider, RHFTextField, RHFSelect } from '../../../components/hook-form';
import { bulkUpdateOrders } from '../../../store/actions/BulkActions';
import MainCard from '../../../ui-component/cards/MainCard';
import useStyles from '../styles';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import useUserRole from '../../../hooks/useUserRole';

const STATUS_ITEMS = [
    { id: 0, status: 'DELIVERED' },
    { id: 1, status: 'CANCELLED' }
];

const validationSchema = yup.object({
    status: yup.string().required(),
    orderIdCsv: yup.string().required()
});

export default function OrderBulkUpdate() {
    useUserRole({ roleList: ['ADMIN', 'SUPER_ADMIN'] });
    const classes = useStyles();
    const dispatch = useDispatch();
    const history = useHistory();
    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(validationSchema),
        defaultValues: ''
    });

    const { reset, handleSubmit } = methods;

    const onSubmit = (data) => {
        const payload = {
            status: data?.status,
            orderIdCsv: data?.orderIdCsv
        };
        dispatch(
            bulkUpdateOrders(payload, () => {
                reset();
                history.replace('/admin/orders/all-orders');
            })
        );
    };

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <AssignmentReturn className={classes.titIeIcon} />
                    <span className={classes.pageTitle}>&nbsp;Bulk Orders Update</span>
                </div>
                <div>
                    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                        <Paper elevation={3} style={{ padding: 20 }}>
                            <div>
                                <label>
                                    Status<span className={classes.span}>*</span>
                                </label>
                            </div>
                            <div>
                                <RHFSelect name="status" label="">
                                    <option value="">Select Status</option>
                                    {STATUS_ITEMS?.map((item, index) => (
                                        <option key={index} value={item?.status}>
                                            {item?.status}
                                        </option>
                                    ))}
                                </RHFSelect>
                            </div>
                            <br />
                            <div>
                                <label>
                                    Order id:<span className={classes.span}>*</span>
                                </label>
                                <RHFTextField name="orderIdCsv" multiline minRows={4} placeholder="example:- 00O001, 000002, 000003" />
                            </div>
                            <br />
                            <div className={classes.submitButton}>
                                <Button variant="contained" color="primary" type="submit" className={classes.btnUpdate}>
                                    Update
                                </Button>
                            </div>
                        </Paper>
                    </FormProvider>
                </div>
            </MainCard>
        </>
    );
}
